import autoBind from 'auto-bind';

export default class {
  constructor(element) {
    autoBind(this);
    this.element = element;
    element.addEventListener('input', this.makeUppercase);
    this.makeUppercase();
  }

  makeUppercase() {
    let tmp =this.element.value;

    tmp = tmp.replace("", "");
    tmp = tmp.replace("", "");
    tmp = tmp.replace("", "");
    tmp = tmp.replace("", "");
    tmp = tmp.replace("", "");
    tmp = tmp.replace("", "");
    tmp = tmp.replace("", "");

    tmp = tmp.toUpperCase();

    this.element.value = tmp;
  }
}
